<template lang="html">
  <div class="convert-ad-rate">
    <!-- 头部 -->
    <default-head header="召币兑换广告费"></default-head>
    <count-item class="borderB" textLeft="我的召币" textRight="兑换明细" :isShow="true">
      <span class="middle wrongColor">1148币</span>
    </count-item>

    <div class="convert-ad-form">
      <div class="text-box" >
        <flm-form-input width="2.3rem" label="兑换广告费数额" placeholder="请输入你要兑换数量" textAlign="right" fontS=".3rem"></flm-form-input>
      </div>
      <div class="text-box" >
        <flm-form-input type="password" label="支付密码" placeholder="请输入支付密码" textAlign="right"></flm-form-input>
      </div>
      <p class="convert-ad-warn">备注：兑换比例1召币=1广告费,最低不得少于1召币</p>

      <sub-button marginTop="1rem" text="立即兑换"  borderRadius=".05rem" @click.native="$router.push('/convertresult')"></sub-button>
    </div>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import countItem from '@/components/personcenter/countItem'
import subButton from '@/components/common/subButton'

export default {
  data () {
    return {
    }
  },
  components: { defaultHead, countItem, subButton }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.convert-ad-rate {
  padding-top: .88rem;
  .middle {
    font-size: .36rem;
  }
  .convert-ad-form {
    .text-box {
      background: @whiteColor;
      padding: 0 .22rem;
      border-bottom: 1px solid @lineColor;
    }
    .text-box:last-child {
      border-bottom: none
    }
    .convert-ad-warn {
      padding-left: .22rem;
      margin-top: .3rem;
      font-size: .3rem;
      color: @lightColor;
    }
  }

}

</style>



